.header {
  width: 100%;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #3c4d53;
  padding: 0 10%;
}

.logo {
  font-size: 2rem;
  color: rgb(255, 255, 255);
  font-weight: bold;
}

.header ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: baseline;
}

.header li {
  margin-left: 3rem;
}

.header a {
  text-decoration: none;
  font-size: 1.5rem;
  color: rgb(255, 241, 241);
}

.header a:hover,
.header a:active,
.header a.active {
  color: rgb(26, 22, 22);
}

.badge {
  color: rgb(255, 241, 241);
  border-radius: 12px;
  padding: 0 1rem;
  margin-left: 0.5rem;
}
@media screen and (max-width: 480px) {
  .header a,
  .header ul {
    width: 100%;
    font-size: small;
  }
}
